.technos {
	position: absolute;
	top: 100px;
	right: 100px;
	width: 500px;
	height: 400px;
	z-index: -1;

	.technos-img {
		opacity: 0.9;
		position: absolute;
		width: 40px;
	}

	:nth-child(1) {
		animation: scale 8s;
		animation-iteration-count: infinite;
		top: 0;
		left: 0;
		width: 50px;
	}
	:nth-child(2) {
		animation: scale 6.5s;
		animation-iteration-count: infinite;
		bottom: 0;
		left: 12px;
		width: 70px;
	}
	:nth-child(3) {
		animation: scale 6.2s;
		animation-iteration-count: infinite;
		bottom: 10px;
		right: 0;
		width: 70px;
	}
	:nth-child(4) {
		animation: scale 8s;
		animation-iteration-count: infinite;
		top: 25px;
		right: 35px;
		width: 70px;
	}
	:nth-child(5) {
		animation: scale 6.5s;
		animation-iteration-count: infinite;
		top: 125px;
		right: 305px;
		width: 70px;
	}
	:nth-child(6) {
		animation: scale 7.7s;
		animation-iteration-count: infinite;
		top: 235px;
		right: 105px;
		width: 70px;
	}
	:nth-child(7) {
		animation: scale 7.1s;
		animation-iteration-count: infinite;
		top: 65px;
		right: 160px;
		width: 70px;
	}
	:nth-child(8) {
		animation: scale 8.2s;
		animation-iteration-count: infinite;
		bottom: 70px;
		left: 150px;
		width: 70px;
	}

	@keyframes scale {
		0% {
			opacity: 0;
			transform: scale(1);
		}
		50% {
			opacity: 1;
			transform: scale(1.2);
		}
		100% {
			opacity: 0;
			transform: scale(1);
		}
	}
}

@media screen and (max-width: 770px) {
	.technos {
		:nth-child(1) {
			top: -40px;
			left: 150px;
		}
		:nth-child(2) {
			bottom: -60px;
			left: 120px;
		}
		:nth-child(5) {
			top: -60px;
			right: 105px;
			width: 70px;
		}
		:nth-child(6) {
			top: 235px;
			right: 45px;
			width: 70px;
		}
		:nth-child(7) {
			animation: scale 7.1s;
			animation-iteration-count: infinite;
			top: 360px;
			right: 160px;
			width: 70px;
		}
		:nth-child(8) {
			animation: scale 8.2s;
			animation-iteration-count: infinite;
			bottom: 90px;
			left: 150px;
			width: 70px;
		}
	}
}
